<template lang="jade">
.hello
  h1 {{ msg }}
  h3 我的孩子從小就跟著大人吃
  h3 網站上的食譜
  h3 就是我們家的家常料理紀錄
  h3 因此幼兒飲食，請直接參考食譜區
  h3 這區簡單列一些2歲前嬰兒沒長牙～長牙的副食品
  cards(:foods = "babyfoods")
</template>

<script>

import Cards from './Cards.vue'

export default {
  name: 'Baby',
  props: ['foods'],
  components: { Cards },
  data () {
    return {
      msg: '嬰幼兒自然美食DIY',
      mySearch: '',
      showPop: false,
      pop: ''
    }
  },
  computed: {
    babyfoods: function () {
      return this.foods.filter(function (f) { return f.b })
    }
  },
  methods: {
    has: function (j, k) {
      if (!k) return true
      return JSON.stringify(j).indexOf(k) > -1
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.animated .card {
  position: relative;
  top: 0;
}
.animated .card:hover {
  top: -2px;
  transition: all 0.3s ease;
}
</style>



